import React, { useState } from 'react'
import { Route, Routes } from 'react-router-dom'

import SectionService from './SectionService'

import VideoComponent from './VideoComponent'
import { newsData } from '../../Components/StaticData'
import { contextVar } from '../../Components/ContextVar'
import AboutJharkhand from './AboutJharkhand'
import Gallery from './Gallery'
import SectionLeaderJharkhand from './SectionLeaderJharkhand'

import LandingNavbar from '../Header/LandingNavbar'
import Footer from '../Footer/Footer'
import SmallScreenMenu from '../Header/SmallScreenMenu'

function LandingRoute() {

  const [ulbData, setulbData] = useState(newsData)
  return (
    <>
      {/* <Routes> */}
      {/* <Route path='/home' element={<LandingNavbar />} /> */}

      <contextVar.Provider value={{ ulbdatafun: setulbData, ulbdata: ulbData }}>
        <LandingNavbar />
        <VideoComponent />
        <SmallScreenMenu />
        <SectionService />
        <AboutJharkhand />
        <Gallery />
        <SectionLeaderJharkhand />
        <Footer/>
      </contextVar.Provider >
      {/* </Routes> */}
    </>
  )
}

export default LandingRoute